@use '../abstracts/' as *;

body {
  position: relative;
  background-color: var(--clr-dark-cyan);
  background-image: url(../images/bg-pattern-top.svg),
    url(../images/bg-pattern-bottom.svg);
  background-repeat: no-repeat;
  background-position: right 40vw top -600px, left 42vw top 230px;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 800px;
    opacity: 0.4;
    z-index: -1;
    background: url(../design/mobile-design.jpg);
  }
}
